import { Accordion, AccordionDetails, AccordionSummary, AppBar, Button, Card, CardContent, Divider, Grid, List, ListItem, ListItemIcon, ListItemText, Modal, Paper, Tab, Tabs, TextField, Typography } from "@material-ui/core";
import { navigate } from "gatsby";
import * as React from "react";
import ajax, { useFetcher } from "../../../components/ajax";
import { useModalTheme } from "../../../components/style";
import Defer from "../../../components/ui/Defer";
import PinPrompt from "../../../components/ui/market/PinPrompt";
import Navbar from "../../../components/ui/Navbar";
import { rupiah } from "../../../components/util";
// @ts-ignore
import icon from '../../../images/Icon Listrik.png';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const Page = () => {
    const modalTheme = useModalTheme();
    const [cust, setCust] = React.useState(null);
    const [prompt, setPrompt] = React.useState(0);
    const data = useFetcher(cust ? '/biller/pln/postpaid?meterid=' + cust : null, null, (err) => {
        alert(err.message);
        setCust(null);
    });

    const check = (e) => {
        e.preventDefault();
        setCust(e.currentTarget.account.value);
    }
    const submit = (pin) => {
        setPrompt(3);
        ajax.post('/biller/pln/postpaid', {
            meterid: data.meterid,
            pin: pin,
        }).then(x => {
            alert('Berhasil');
            navigate('/digital/history/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
            setPrompt(2);
        });
    }
    return (
        <div>
            <Navbar />
            <form style={{ padding: '1em' }} onSubmit={check}>
                <List>
                    <ListItem>
                        <ListItemIcon><img src={icon} height="40px" alt="" /></ListItemIcon>
                        <ListItemText primary="Beli Listrik Non-Tagihan PLN" />
                    </ListItem>
                    <ListItem>
                        <TextField name="account" label="Nomor Registrasi" variant="filled" fullWidth />
                    </ListItem>
                    <ListItem>
                        <Button type="submit" variant="contained" color="primary">
                            Cek
                        </Button>
                        <Button style={{ marginLeft: 'auto' }} variant="outlined" onClick={() => navigate('/user/')}>
                            Kembali
                        </Button>
                    </ListItem>
                </List>
            </form>
            <Defer data={data} visible={cust}>
                {() => (
                    <Paper elevation={5}><List>
                        <ListItem>
                            <ListItemText primary={'No. Registrasi'} />
                            <ListItemText primary={data.details.noreg} className={modalTheme.right} />
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Nama'} />
                            <ListItemText primary={data.details.nama} className={modalTheme.right} />
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Jenis Pembayaran'} />
                            <ListItemText primary={data.details.jenis} className={modalTheme.right} />
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Tagihan'} />
                            <ListItemText primary={rupiah(data.bill)} className={modalTheme.right} />
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Admin'} />
                            <ListItemText primary={rupiah(data.admin)} className={modalTheme.right} />
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Total Tagihan'} />
                            <ListItemText primary={rupiah(data.total)} className={modalTheme.right} primaryTypographyProps={{ variant: 'h6' }} />
                        </ListItem>
                        <ListItem>
                            <Accordion style={{ width: '100%' }}>
                                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                                    <Typography variant="h6">
                                        Rincian Tagihan
                                    </Typography>
                                </AccordionSummary>
                                <AccordionDetails style={{ flexDirection: 'column' }}>
                                    <List>

                                        {data.details.tagihan.map(x => (
                                            <>
                                                <ListItem>
                                                    <ListItemText primary={'Periode'} />
                                                    <ListItemText primary={x.periode} className={modalTheme.right}
                                                    primaryTypographyProps={{ variant: 'h6' }} />
                                                </ListItem>
                                                <ListItem>
                                                    <ListItemText primary={'Tagihan'} />
                                                    <ListItemText primary={rupiah(x.bill)} className={modalTheme.right} />
                                                </ListItem>
                                                <ListItem>
                                                    <ListItemText primary={'Admin'} />
                                                    <ListItemText primary={rupiah(x.admin)} className={modalTheme.right} />
                                                </ListItem>
                                                <ListItem>
                                                    <ListItemText primary={'Denda'} />
                                                    <ListItemText primary={rupiah(x.denda)} className={modalTheme.right} />
                                                </ListItem>
                                                <ListItem divider>
                                                    <ListItemText primary={'Total'} />
                                                    <ListItemText primary={rupiah(x.total)} className={modalTheme.right} />
                                                </ListItem>
                                            </>
                                        ))}
                                    </List>
                                </AccordionDetails>
                            </Accordion>
                        </ListItem>
                        <ListItem>
                            <Button fullWidth color="primary" variant="contained" style={{ marginLeft: 'auto' }} onClick={() => setPrompt(2)}>Beli</Button>
                        </ListItem>
                    </List></Paper>
                )}
            </Defer>
            <PinPrompt open={prompt >= 2} submitting={prompt === 3} onClose={() => setPrompt(0)} onAction={submit} />
        </div >
    );
}

export default Page
